.title-xl {
	font-family: $montserrat;
	@include f(60, 1.034, 2.1, 400);
	@include sm {
		font-size: 22px; } }

.title-lg {
	font-family: $montserrat;
	@include f(36, 1.034, 1.3, 400); }

.title-sm {
	font-family: $montserrat;
	@include f(16, 1.25, 2.4, 400);
	@include sm {
		@include f(12, 15px, 1.8); } }

.awesome-title {
	position: relative;
	padding-left: 36px;
	&:before {
		content: '';
		position: absolute;
		top: 5px;
		left: 0;
		bottom: 5px;
		border-left: 11px solid $green; }
	strong {
		display: block; } }

.description {
	opacity: 0.9;
	@include f(15, 1.27); }
